#root {
	padding: 0 0 50px;
}
.mb-15 {
	margin-bottom: 15px;
}
.mt-15 {
	margin-top: 15px;
}
.mr-5 {
	margin-right: 5px;
}
.ml-5 {
	margin-left: 5px;
}
.pr-5 {
	padding-right: 5px;
}
.dropdown-menu .fa:before {
	padding-right: 10px;
}
.text-right {
	float: right;
	cursor: pointer;
}
.btn-close:hover {
	color: red;
}
.sort-selected::after {
	content: '\F00C';
	font-family: FontAwesome;
	font-weight: normal;
	font-style: normal;
	margin: 0px 0px 0px 10px;
	display: inline-block;
}
.label-status {
	cursor: pointer;
}
.search-wrp {
	position: relative;
}
.search-wrp .btn-clear {
	display: inline-block;
	vertical-align: middle;
	position: absolute;
	right: 75px;
	top: 50%;
	-webkit-transform: translate(0, -50%);
	-moz-transform: translate(0, -50%);
	-ms-transform: translate(0, -50%);
	-o-transform: translate(0, -50%);
	transform: translate(0, -50%);
	width: 20px;
	height: 20px;
	background: transparent;
	border: 0;
	outline: 0;
	text-align: center;
	padding: 0;
	margin: 0;
	z-index: 2;
}
.dropdown-menu>li>span {
	display: block;
	padding: 3px 20px;
	clear: both;
	font-weight: 400;
	line-height: 1.42857143;
	color: #333;
	white-space: nowrap;
	cursor: pointer;
}
.dropdown-menu>li>span:focus,
.dropdown-menu>li>span:hover {
	color: #262626;
	text-decoration: none;
	background-color: #f5f5f5;
}
